import moment from 'moment'
import BigNumber from 'bignumber.js'
const contractStartBlock = 11116308
const secondperBlock = 10
const incrementforOneDay = (60 / secondperBlock) * 60 * 24

export const cleanData = (data, web3) => {
  const asyncFunctions = []
  const newData = []
  const calculate = async (ele) => {
    const txnEth = await web3.utils.fromWei(ele.returnValues.txnEth, 'ether')
    const ETHUSD = await web3.utils.fromWei(ele.ETH_USD, 'ether')
    newData.push({
      txnEth: BigNumber(ETHUSD).times(txnEth).toString(),
      blockNumber: ele.blockNumber,
      timeStamp: ele.timeStamp
    })
  }
  for (let i = 0; i < data.length; i++) {
    const ele = data[i]
    asyncFunctions.push(calculate(ele))
  }
  const dailyEthData = []
  const txnCount = []
  return Promise.all(asyncFunctions).then(() => {
    console.log('finish')
    let txnData = []
    let total = 0
    let count = 1
    for (let i = 0; i < newData.length; i++) {
      const element = newData[i]
      const currentMin = contractStartBlock + incrementforOneDay * (count - 1)
      const currentMax = contractStartBlock + incrementforOneDay * count
      if (
        element.blockNumber > currentMin &&
        element.blockNumber < currentMax
      ) {
        total += Number(element.txnEth)
        txnData.push(element)
      } else if (element.blockNumber > currentMax) {
        txnCount.push({
          transactions: txnData.length,
          timeStamp: moment(element.timeStamp * 1000).format('YYYY-MM-DD')
        })
        txnData = []
        dailyEthData.push({
          total,
          timeStamp: moment(element.timeStamp * 1000).format('YYYY-MM-DD')
        })
        total = 0
        count++
      }
    }
    return { dailyEthData, txnCount }
  })
}

export const mergeTwoData = (volumn, price) => {
  const mergedData = []
  for (let i = 0; i < price.length; i++) {
    const element = price[i]
    const data = {
      ...element,
      ...volumn.find((ele) => ele.timeStamp === element.timeStamp)
    }
    if (!data.total) {
      data.total = 0
    }
    mergedData.push(data)
  }
  return mergedData
}
